 .customer {
    width: 100%;
 }

 .client_photo {
    justify-content: center;
    margin: 0 auto;
    margin-bottom: 32px;
    border-radius: 50%;

    
   
    
 }
 
 .customer_text {
    display: block;
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.86;
    letter-spacing: 4%;

    justify-content: center;
     align-items: center;
     padding: 39px 35px;
     margin-bottom: 32px;
     border-radius: 24px;
     display: block;
     background-color: #84A178;
     color: #FCFCFC;

    

     @media screen and (min-width: 768px) {
        padding: 29px 80px;
        
    }

    @media screen and (min-width: 1200px) {
       
        padding: 43px 109px;

        
    }
 }

